.App {
	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 7s linear;
	        animation: App-logo-spin infinite 7s linear;
	height: 64px;
}

.App-header {
	-ms-flex-direction: row;
	    flex-direction: row;
	background-color: #222;
	height: 100px;
	padding: 20px;
	color: white;

	 {
		-ms-flex: 1 1;
		    flex: 1 1;
	}
}

.App-title {
	font-size: 1.5em;
}

.App-intro {
	/* font-size: large; */
	font-family: 'Libre Baskerville', 'Lora', 'Calisto MT', 'Bookman Old Style', Bookman, 'Goudy Old Style', Garamond,
		'Hoefler Text', 'Bitstream Charter', Georgia, serif;
}

.App-desciption {
	font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Item-info {
	padding-left: 10px;
	padding-right: 10px;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}



